.friend-links {
  overflow: auto;
  padding: 10px 10px 0;
  text-align: center
}
[data-theme=dark] .friend-link {
  border: 1px solid #0000001c;
  background-color: rgba(0,0,0,0.161);
}
.friend-link {
  position: relative;
  float: left;
  overflow: hidden;
  line-height: 17px;
  -webkit-transform: translateZ(0);
  height: 100px;
  padding: 10px;
  width: calc(100% / 4 - .5rem);
  margin: .5rem .25rem;
  border-radius: 12px;
  border: 1px solid #e3e8f7;
  background-color: #fff;
  box-shadow:inset -3px -3px 0px 0px #2c2d301a;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out
}
@media screen and (max-width: 1024px) {
  .friend-links>.friend-link {
      width:calc(50% - 15px)!important
  }
}

@media screen and (max-width: 600px) {
  .friend-links>.friend-link {
      width:calc(100% - 15px)!important
  }
}
.friend-links>.friend-link:hover {
  border-color: #3b70fc!important;
  background-color: #3b70fc!important;
  -webkit-box-shadow: #3b70fc!important;
  box-shadow: #3b70fc!important
}
.friend-links>.friend-link:hover .friend-link-icon {
  width: 0;
  height: 0;
  margin-left: -10px
}
.friend-links>.friend-link:hover .friend-link-desc,.friend-links>.friend-link:hover .friend-link-name {
  color: #363636
}

.friend-links>.friend-link:active:before,.friend-links>.friend-link:focus:before,.friend-links>.friend-link:hover:before {
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -o-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1)
}
.friend-links>.friend-link a {
  color: var(--font-color);
  text-decoration: none
}
.friend-links>.friend-link a .friend-link-icon {
  float: left;
  overflow: hidden;
  margin: 15px 10px;
  width: 60px;
  height: 60px;
  border-radius: 35px;
  -webkit-transition: all .3s ease-out;
  -moz-transition: all .3s ease-out;
  -o-transition: all .3s ease-out;
  -ms-transition: all .3s ease-out;
  transition: all .3s ease-out;
  margin: 8px 0 8px 0;
  border-radius: 50%;
  overflow: hidden
}
.friend-links>.friend-link a .friend-link-icon img {
  width: 100%;
  height: 100%;
  -webkit-transition: filter 375ms ease-in .2s,-webkit-transform .3s;
  -moz-transition: filter 375ms ease-in .2s,-moz-transform .3s;
  -o-transition: filter 375ms ease-in .2s,-o-transform .3s;
  -ms-transition: filter 375ms ease-in .2s,-ms-transform .3s;
  transition: filter 375ms ease-in .2s,transform .3s;
  object-fit: cover
}
.friend-link-info {
  display: -webkit-box;
  display: -moz-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-lines: multiple;
  -moz-box-lines: multiple;
  -o-box-lines: multiple;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding-left: 10px;
  text-align: left;
  -webkit-box-orient: vertical;
  -moz-box-orient: vertical;
  -o-box-orient: vertical;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column
}
.friend-link-info .friend-link-name {
  padding: 12px 0 16px 0;
  height: auto;
  font-weight: 700;
  font-size: 1em;
}
.friend-link-info .friend-link-desc {
  padding: 0;
  height: 35px;
  font-size: .93em;
  opacity: .7;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=70)";
  filter: alpha(opacity=70);
  word-break: break-all;
  white-space: break-spaces;
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2
}
.flink-name {
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 1.5em
}
