@import '_page/friends';
@import '_page/home';
@import '_partials/_single/code';
//背景图片
body{
  background-image: url(https://npm.elemecdn.com/xrhugo@1.0.8/public/img/bg.svg);
  background-repeat: initial;
  background-size: 25rem;
}
body[data-theme='dark']{
  background-image: url(https://npm.elemecdn.com/xrhugo@1.0.8/public/img/bg1.svg);
}
//文章图片居中
.single img{
  display: block;
  margin: 0 auto 20px;
  border-radius: 5px;
}
//介绍字体居中
p.p.center,span.p.center {
  display: block;
  text-align: center;
}
p.p.center,span.p.center {
  display: block;
  text-align: center;
}
p.p.large,span.p.large {
  font-size: 2.5rem;
  line-height: 1.4
}
p.p.huge,p.p.large,p.p.small,p.p.ultra,span.p.huge,span.p.large,span.p.small,span.p.ultra {
  margin: 0;
  padding: 0;
}
*{
  -webkit-box-sizing:border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

[data-theme=dark] .author-content-item {
    background-color: rgba(0,0,0,.161)
  }
  #page:has(#about-page) {
    border: 0;
    box-shadow: none !important;
    padding: 50px 40px !important;
  }
  
  #page:has(#about-page) .page-title {
    display: none;
  }
  
  .page:has(#about-page) #footer-wrap {
    opacity: 1;
    overflow: visible;
    height: auto !important;
    min-height: 16px;
  }
  #about-page .author-box {
    position: relative;
  }
  #about-page .author-box .author-img {
    margin: auto;
    border-radius: 50%;
    overflow: hidden;
    width: 125px;
    height: 125px;
  }
  #about-page .author-box .author-img img {
    border-radius: 50%;
    overflow: hidden;
    width: 125px;
    height: 125px;
  }
  
  #about-page .author-box .image-dot {
    width: 25px;
    height: 25px;
    background: #6bdf8f;
    position: absolute;
    border-radius: 50%;
    border: 3px solid #fff;
    top: 68%;
    left: 49%;
    transform: translate(38px, 12px);
  }
  
  .author-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    margin-top: 1rem;
  }
  
  #about-page .myInfoAndSayHello {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: #fff;
    background: linear-gradient(120deg, #5b27ff 0, #00d4ff 100%);
    background-size: 200%;
    animation: gradient 15s ease infinite;
    width: 59%;
  }
  
  .author-content-item {
    width: 49%;
    border-radius: 24px;
    background: #fff;
    // border: .5px solid #e3e8f71e;
    box-shadow: 0 8px 16px -4px #2c2d301a;
    position: relative;
    padding: 1rem 2rem;
    overflow: hidden;
  }
  
  #about-page .myInfoAndSayHello .title1 {
    opacity: 0.8;
    line-height: 1.3;
  }
  
  #about-page .myInfoAndSayHello .title2 {
    font-size: 36px;
    font-weight: 700;
    line-height: 1.1;
    margin: 0.5rem 0;
  }
  .inline-word {
    word-break: keep-all;
    white-space: nowrap;
  }
  
  #about-page .myInfoAndSayHello .title1 {
    opacity: 0.8;
    line-height: 1.3;
  }
  
  .author-content-item.aboutsiteTips {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 39%;
  }
  
  .aboutsiteTips h2 {
    margin-right: auto;
    font-size: 36px;
    font-family: Helvetica;
    line-height: 1.06;
    letter-spacing: -0.02em;
    color: var(--font-color);
    margin-top: 0;
  }
  
  .aboutsiteTips .mask {
    height: 36px;
    position: relative;
    overflow: hidden;
    margin-top: 4px;
  }
  .aboutsiteTips .mask span {
    display: block;
    box-sizing: border-box;
    position: absolute;
    top: 36px;
    padding-bottom: var(--offset);
    background-size: 100% 100%;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-repeat: no-repeat;
  }
  .aboutsiteTips .mask span[data-show] {
    transform: translateY(-100%);
    transition: 0.5s transform ease-in-out;
  }
  .aboutsiteTips .mask span[data-up] {
    transform: translateY(-200%);
    transition: 0.5s transform ease-in-out;
  }
  .aboutsiteTips .mask span:nth-child(1) {
    background-image: linear-gradient(45deg, #0ecffe 50%, #07a6f1);
  }
  .aboutsiteTips .mask span:nth-child(2) {
    background-image: linear-gradient(45deg, #18e198 50%, #0ec15d);
  }
  .aboutsiteTips .mask span:nth-child(3) {
    background-image: linear-gradient(45deg, #8a7cfb 50%, #633e9c);
  }
  .aboutsiteTips .mask span:nth-child(4) {
    background-image: linear-gradient(45deg, #fa7671 50%, #f45f7f);
  }
  #about-page .about-statistic {
    min-height: 380px;
    background: url(https://npm.elemecdn.com/xrhugo@1.0.7/public/img/bg.webp) no-repeat top;
    background-size: cover;
    color: #fff;
    overflow: hidden;
  }
  #about-page .about-statistic::after {
    box-shadow: 0 -159px 173px 71px #0c1c2c inset;
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  .author-content-item .card-content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 1rem 2rem;
  }
  
  .author-content-item .card-content .author-content-item-title {
    margin-bottom: 0.5rem;
  }
  .author-content-item .author-content-item-title {
    font-size: 36px;
    font-weight: 700;
    line-height: 1;
  }
  #statistic {
    font-size: 16px;
    border-radius: 15px;
    width: 100%;
    color: var(--anzhiyu-white);
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 1rem;
    margin-bottom: 2rem;
  }
  #statistic div span:first-child {
    opacity: 0.8;
    font-size: 0.6rem;
  }
  #statistic div span:last-child {
    font-weight: 700;
    font-size: 34px;
    line-height: 1;
    white-space: nowrap;
  }
  #statistic div {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 50%;
    margin-bottom: 0.5rem;
  }
  
  .post-tips {
    color: #999999;
    font-size: 14px;
    position: absolute;
    bottom: 1rem;
    left: 2rem;
  }
  .post-tips a {
    color: #999999 !important;
    border: none !important;
  }

  .author-content-item.personalities {
    position: relative;
    width: 59%;
  }
  
  .author-content-item.personalities .image {
    position: absolute;
    right: 30px;
    top: 10px;
    width: 220px;
    transition: transform 2s cubic-bezier(0.13, 0.45, 0.21, 1.02);
  }
  .author-content-item.personalities .image img {
    display: block;
    margin: 0 auto 20px;
    max-width: 100%;
    transition: filter 375ms ease-in 0.2s;
  }
  .author-content-item.personalities:hover .image {
    transform: rotate(-10deg);
  }
  .author-content-item:hover .card-background-icon {
    transform: rotate(20deg);
  }
  .author-content-item.personalities .title2 {
    font-size: 36px;
    font-weight: 700;
    line-height: 1.1;
  }
  
  .author-content-item.single {
    width: 100%;
  }
  .author-content-item-group.column {
    display: flex;
    flex-direction: column;
    width: 49%;
    justify-content: space-between;
  }
  
  .post-tips a:hover {
    color: var(--anzhiyu-main) !important;
    background: none !important;
  }
  .author-content-item.maxim {
    font-size: 36px;
    font-weight: 700;
    line-height: 1.1;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    width: 39%;
  }
  .author-content-item .author-content-item-tips {
    opacity: 0.8;
    font-size: 0.6rem;
    margin-bottom: 0.5rem;
  }
  .author-content-item.maxim .maxim-title {
    display: flex;
    flex-direction: column;
  }
 
  /* .card-background-icon {
    font-size: 12rem;
    opacity: 0.2;
    position: absolute;
    right: 0;
    bottom: -40%;
    transform: rotate(30deg);
    transition: 2s ease-in-out;
  } */
  .author-content-item .content-bottom {
    margin-top: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .author-content-item .content-bottom .icon-group {
    display: flex;
    position: relative;
  }
  .author-content-item .content-bottom .icon-group i {
    display: inline-block;
    width: 143px;
    height: 18px;
    margin-right: 0.5rem;
  }
  
  @media screen and (max-width: 768px) {
    #gitcalendar {
      display: none;
    }
    .author-content {
      margin-top: 0;
    }
    .author-content-item {
      width: 100% !important;
      margin-top: 1rem;
      padding: 1rem;
    }
    .author-content-item-group.column {
      width: 100% !important;
    }
    .author-content-item.personalities {
      height: 170px;
    }
    .post-tips {
      left: auto;
    }
    .author-content-item.personalities .image {
      width: 200px;
      right: 0px;
      top: 30px;
    }
    .site-card-group > a {
      width: 100% !important;
    }
    .post-reward {
      display: none;
    }
    .reward-list-item {
      width: 100% !important;
    }
    .layout > div:first-child:not(.recent-posts) {
      /* border-radius: 0; */
      padding: 0 1rem !important;
      box-shadow: none !important;
      background: var(--anzhiyu-background);
    }
  }
  
  @media screen and (min-width: 768px) and (max-width: 896px) {
    .author-content-item.like-music .content-bottom .tips {
      display: none;
    }
  }
  //skills
  .author-content-item.skills {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 50%;
    min-height: 450px;
  }
  
  .author-content-item.skills .skills-style-group {
    position: relative;
  }
  
  .author-content-item.skills .tags-group-all {
    display: flex;
    transform: rotate(0);
    transition: 0.3s;
  }
  .author-content-item.skills .tags-group-wrapper {
    margin-top: 40px;
    display: flex;
    flex-wrap: nowrap;
    animation: rowup 60s linear infinite;
  }
  .tags-group-icon-pair {
    margin-left: 1rem;
  }
  .tags-group-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 66px;
    font-weight: 700;
    box-shadow: 0 2px 16px -3px rgba(0, 0, 0, .15);
    width: 120px;
    height: 120px;
    border-radius: 30px;
  }
  .tags-group-icon img {
    width: 60%;
    margin: 0 auto !important;
  }
  .tags-group-icon-pair .tags-group-icon:nth-child(even) {
    margin-top: 1rem;
    transform: translate(-60px);
  }
  .author-content-item.skills .skills-list {
    display: flex;
    opacity: 0;
    transition: 0.3s;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 10px;
  }
  .author-content-item.skills .skill-info {
    display: flex;
    align-items: center;
    margin-right: 10px;
    margin-top: 10px;
    border-radius: 40px;
    padding: 4px 12px 4px 8px;
    box-shadow: 0 2px 16px -3px rgba(0, 0, 0, .15);
  }
  .author-content-item.skills .skill-icon {
    width: 32px;
    height: 32px;
    border-radius: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 8px;
  }
  .author-content-item.skills .skill-icon img {
    width: 18px;
    height: 18px;
    margin: 0 auto !important;
  }
  .author-content-item.skills .etc {
    margin-right: 10px;
    margin-top: 10px;
  }
  
  @keyframes rowup {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-50%);
    }
  }
  .author-content-item.skills:hover .skills-style-group .tags-group-all {
    opacity: 0;
  }
  .author-content-item.skills:hover .skills-style-group .skills-list {
    opacity: 1;
  }